.assignment {
  font-size: 24px;
  font-weight: 700;
}

.link-dark {
  color: #212529;
}

.chapter-text, .chapter-name {
  font-size: 12px;
}

.comments-text {
  font-size: 12px;
}

.mytime {
  font-size: 12px;
}

.text-width {
  font-size: 14px;
  line-height: 21px;
}

.hwbtn {
  width: 176px;
}

.Emir-img {
  width: 141px;
}

.name-emir {
  font-size: 24px;
  line-height: 36px;
}

.mail-emir {
  font-size: 14px;
}

.dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  max-height: 0px;
  transition: all 0.8s;
  overflow: hidden;
  z-index: 120;
}

.dropdown.active {
  max-height: 300px;
}

.more {
  transition: all .3s;
}

.verified-link, .more:hover {
  cursor: pointer;
}

.less {
  transition: all .3s;
  transform: rotate(180deg);
}
